import { ethers } from "ethers";
import Contracts from "./address.json";
import IERC20 from "./abi/EIP20Interface.json";
import IComptroller from "./abi/ComptrollerG6.json";
import CERC20 from "./abi/CErc20.json";
import IOracle from "./abi/SimplePriceOracle.json";
import CompoundLens from "./abi/CompoundLens.json";

const provider = new ethers.providers.Web3Provider(window.ethereum)
const signer = provider.getSigner()
window.provider = provider;
window.signer = signer;

const AddressZero = ethers.constants.AddressZero;
const cTokenTemplate = new ethers.Contract(AddressZero, CERC20);
const IERC20Template = new ethers.Contract(AddressZero, IERC20);
const IComptrollerTemplate = new ethers.Contract(AddressZero, IComptroller);
const IOracleTemplate = new ethers.Contract(AddressZero, IOracle);
const CompoundLensTemplate = new ethers.Contract(AddressZero, CompoundLens);

const decimals = n => ethers.BigNumber.from(`1${'0'.repeat(n)}`);
const D18 = decimals(18);

const blockTime = 2;
const APY = perBlock => Math.round(((Number(perBlock.mul(24 * 3600 / blockTime)) / 1e18 + 1) ** 365 - 1) * 10000);

class CToken {
    constructor(cToken, underlying, comptroller, oracle) {
        const provider = new ethers.providers.Web3Provider(window.ethereum)
        const signer = provider.getSigner()
        this.cToken = cTokenTemplate.attach(cToken).connect(signer);
        this.underlying = IERC20Template.attach(underlying).connect(signer);
        this.comptroller = IComptrollerTemplate.attach(comptroller).connect(signer);
        this.oracle = IOracleTemplate.attach(oracle).connect(signer);
    }
    async getAccount() {
        return window.ethereum.enable().then(accs => accs[0]);
    }

    // 
    async mint(amount) {
        const account = await this.getAccount();
        const allowance = await this.underlying.allowance(account, this.cToken.address);
        if (allowance.lt(amount))
            await this.enableMarket();
        return this.cToken.mint(amount)
    }
    // 
    async redeem(amount, useCtoken = false) {
        if (useCtoken)
            return this.cToken.redeem(amount);
        else
            return this.cToken.redeemUnderlying(amount);
    }

    // 
    async borrow(amount) {
        return this.cToken.borrow(amount);
    }
    // 
    async repayBorrow(amount) {
        const account = await this.getAccount();
        const allowance = await this.underlying.allowance(account, this.cToken.address);
        if (allowance.lt(amount))
            await this.enableMarket();
        return this.cToken.repayBorrow(amount);
    }

    // 
    async enterMarket() {
        return this.comptroller.enterMarkets([this.cToken.address]);
    }
    // 
    async exitMarket() {
        return this.comptroller.exitMarket(this.cToken.address);
    }
    // 
    async getMarketEntered() {
        const account = await this.getAccount();
        return this.comptroller.checkMembership(account, this.cToken.address);
    }

    // 
    async enableMarket() {
        return this.underlying.approve(this.cToken.address, ethers.constants.MaxUint256);
    }
    // 
    async getMarketEnabled() {
        const account = await this.getAccount();
        const threshold = 0;
        const allowance = await this.underlying.allowance(account, this.cToken.address);
        console.log('getMarketEnabled:', threshold, allowance.toString(), allowance.gt(threshold))
        return allowance.gt(threshold);
    }

    // 
    async getBorrowLimit() {
        return 0;
    }

    // (+)
    async getBorrowBalance() {
        return this.cToken.callStatic.totalBorrowsCurrent();
    }
    // 
    async getSupplyBalance() {
        return this.cToken.getCash();
    }
    // 
    async getReserve() {
        return this.cToken.totalReserves();
    }

    // 
    async getAccountSnapshot() { // []
        const account = await this.getAccount();
        const snap = await this.cToken.getAccountSnapshot(account);
        if (Number(snap[0]) != 0) throw `getAccountSnapshot ${snap}`;
        return snap.slice(1);
    }
    // APY 
    async getSupplyAPY() {
        const perBlock = await this.cToken.supplyRatePerBlock();
        return APY(perBlock);
    }
    // APY 
    async getBorrowAPY() {
        const perBlock = await this.cToken.borrowRatePerBlock()
        return APY(perBlock);
    }
}


class CompoundViews {
    constructor(compoundLens, comptrollerAddress, cTokensAddress) {
        this.comptroller = comptrollerAddress;
        this.cTokens = cTokensAddress;
        this.CompoundLens = CompoundLensTemplate.attach(compoundLens).connect(signer);
    }
    cTokenMetadataAll() {
        return this.CompoundLens.callStatic.cTokenMetadataAll(this.cTokens);
    }
    cTokenUnderlyingPriceAll() {
        return this.CompoundLens.callStatic.cTokenUnderlyingPriceAll(this.cTokens);
    }
    cTokenBalancesAll(account) {
        return this.CompoundLens.callStatic.cTokenBalancesAll(this.cTokens, account);
    }
    getAccountLimits(account) {
        return this.CompoundLens.callStatic.getAccountLimits(this.comptroller, account);
    }
    async marketInfo(account = undefined) {
        const percent = (numerator, denominator) => Number(numerator.mul(10000).div(denominator != 0 ? denominator : 1)) / 10000;
        const metaAwait = this.cTokenMetadataAll();
        const priceAwait = this.cTokenUnderlyingPriceAll();
        let balance;
        let limits;
        if (account) {
            const balanceAwait = this.cTokenBalancesAll(account);
            const limitsAwait = this.getAccountLimits(account);
            balance = await balanceAwait;
            limits = await limitsAwait;
        }
        const metas = await metaAwait;
        const prices = await priceAwait;
        let supplyBalance = 0;
        let borrowBalance = 0;
        let netAPY = 0;
        if (account) {
            let supplyInterest;
            let borrowInterest;
            [supplyBalance, borrowBalance, supplyInterest, borrowInterest] = balance.reduce((total, b, i) => {
                const price = prices[i].underlyingPrice.div(D18);
                const supplyValue = b.balanceOfUnderlying.mul(price);
                const borrowValue = b.borrowBalanceCurrent.mul(price);
                total[0] = supplyValue.add(total[0]);
                total[1] = borrowValue.add(total[1]);
                const meta = metas[i];
                total[2] = supplyValue.mul(APY(meta.supplyRatePerBlock)).add(total[2]);
                total[3] = borrowValue.mul(APY(meta.borrowRatePerBlock)).add(total[3]);
                return total;
            }, [0, 0, 0, 0])
            netAPY = supplyBalance > 0 ? supplyInterest.sub(borrowInterest).div(supplyBalance) / 10000 : ethers.constants.Zero;
        }
        const legalBorrowLimit = limits.liquidity ? borrowBalance.add(limits.liquidity) : borrowBalance.sub(limits.shortfall);
        return {
            markets: metas.map((meta, i) => ({
                name: Contracts.LPS[i].name,
                cToken: meta.cToken, // cToken
                exchangeRateCurrent: meta.exchangeRateCurrent, // 
                supplyRatePerBlock: meta.supplyRatePerBlock,  // 
                supplyAPY: APY(meta.supplyRatePerBlock) / 10000,
                borrowRatePerBlock: meta.borrowRatePerBlock,  // 
                borrowAPY: APY(meta.borrowRatePerBlock) / 10000,
                reserveFactorMantissa: meta.reserveFactorMantissa, // 
                totalBorrows: meta.totalBorrows,  // ()
                totalReserves: meta.totalReserves, // 
                totalSupply: meta.totalSupply,   // cToken
                totalCash: meta.totalCash,     // 
                isListed: meta.isListed,      // 
                collateralFactorMantissa: meta.collateralFactorMantissa, // 
                underlyingAssetAddress: meta.underlyingAssetAddress, // 
                cTokenDecimals: meta.cTokenDecimals,  // ctoken
                underlyingDecimals: meta.underlyingDecimals, // 
                underlyingPrice: prices[i].underlyingPrice.div(D18), // 
                balance: balance && {
                    balanceOf: balance[i].balanceOf, // ctoken
                    borrowBalanceCurrent: balance[i].borrowBalanceCurrent, // +
                    balanceOfUnderlying: balance[i].balanceOfUnderlying, // (+)
                    tokenBalance: balance[i].tokenBalance, // 
                    tokenAllowance: balance[i].tokenAllowance, // approve
                    // / USDT
                    underlyingValue: balance[i].balanceOfUnderlying != 0 ?
                        balance[i].balanceOfUnderlying.mul(prices[i].underlyingPrice.div(D18)) :
                        balance[i].borrowBalanceCurrent.mul(prices[i].underlyingPrice.div(D18)),
                    limitPercent: balance[i].balanceOfUnderlying != 0 ?
                        percent(balance[i].balanceOfUnderlying.mul(prices[i].underlyingPrice.div(D18)), legalBorrowLimit) :
                        percent(balance[i].borrowBalanceCurrent.mul(prices[i].underlyingPrice.div(D18)), legalBorrowLimit),
                },
                isCollateral: limits ? limits.markets.indexOf(meta.cToken) > -1 : false
            })),
            limit: limits && {
                markets: limits.markets, // ctoken
                // liquidity  shortfall 0
                liquidity: limits.liquidity, // - 
                shortfall: limits.shortfall, // - 
            },
            supplyBalance,
            borrowBalance,
            netAPY,
            borrowLimit(cToken = 0, amount = 0, typ = 'mint') {
                const meta = this.markets.find(market => market.cToken == cToken);
                let borrowed = this.borrowBalance;
                let liquidity = this.limit.liquidity > 0 ? this.limit.liquidity : ethers.constants.Zero.sub(this.limit.shortfall);
                const changeValue = cToken && meta.underlyingPrice.mul(amount);
                switch (typ) {
                    case 'mint':
                        liquidity = liquidity.add(changeValue);
                        break;
                    case 'redeem':
                        liquidity = liquidity.sub(changeValue);
                        break;
                    case 'borrow':
                        borrowed = borrowed.add(changeValue);
                        liquidity = liquidity.sub(changeValue);
                        break;
                    case 'repay':
                        borrowed = borrowed.sub(changeValue);
                        liquidity = liquidity.add(changeValue);
                        break;
                }
                const totalLimit = borrowed.add(liquidity);
                return [borrowed, totalLimit, percent(borrowed, totalLimit)];
            },
            perentLimit(cToken, typ, percent) {
                let limitAmount;
                const meta = this.markets.find(market => market.cToken == cToken);
                switch (typ) {
                    case 'mint':
                        limitAmount = meta.balance.tokenBalance;
                        break;
                    case 'redeem':
                    case 'borrow':
                        limitAmount = limits.liquidity.div(meta.underlyingPrice);
                        break;
                    case 'repay':
                        limitAmount = meta.balance.borrowBalanceCurrent;
                        break;
                }
                return limitAmount.mul(Math.floor(percent * 10000)).div(10000);
            }
        }
    }
}


export function getMarkets() {
    const comptroller = Contracts.comptroller;
    window.clens = new CompoundViews(Contracts.compoundLens, comptroller, Contracts.LPS.map(LP => LP.cToken));
    const oracle = Contracts.oracle;
    return Contracts.LPS.map(LP => ({
        name: LP.name,
        decimals: LP.decimals,
        collateral: LP.collateral,
        cToken: new CToken(LP.cToken, LP.underlying, comptroller, oracle)
    }));
}

export const contracts = getMarkets();
window.contracts = contracts;