import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/markets',
    name: 'Markets',
    component: () => import(/* webpackChunkName: "markets" */ '../views/Markets.vue')
  },
  {
    path: '/governance',
    name: 'Governance',
    component: () => import(/* webpackChunkName: "governance" */ '../views/Governance.vue')
  },
  {
    path: '/developers',
    name: 'Developers',
    component: () => import(/* webpackChunkName: "developers" */ '../views/Developers.vue')
  },
  {
    path: '/prices',
    name: 'Prices',
    component: () => import(/* webpackChunkName: "prices" */ '../views/Prices.vue')
  },
  {
    path: '/docs',
    name: 'Docs',
    component: () => import(/* webpackChunkName: "docs" */ '../views/Docs.vue')
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: () => import(/* webpackChunkName: "dashboard" */ '../views/Dashboard.vue')
  },
  {
    path: '/vote',
    name: 'Vote',
    component: () => import(/* webpackChunkName: "vote" */ '../views/Vote.vue')
  },

]

// ElementUIvue-router3.0
const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error => error)
}

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
