<template>
  <div class="container public-container-main">
    <el-row :gutter="10" type="flex" justify="space-between" align="center">
      <el-col :xs="20" :sm="6" :md="6" :lg="6" :xl="6" align="left">
        <Logo />
      </el-col>
      <el-col
        :xs="12"
        :sm="12"
        :md="12"
        :lg="12"
        :xl="12"
        class="hidden-xs-only"
      >
        <Nav />
      </el-col>
      <el-col
        :xs="6"
        :sm="6"
        :md="6"
        :lg="6"
        :xl="6"
        align="right"
        class="hidden-xs-only"
      >
        <el-button type="primary" @click="route2apps">App</el-button>
      </el-col>
      <el-col
        :xs="4"
        :sm="0"
        :md="0"
        :lg="0"
        :xl="0"
        class="hidden-sm-only"
        align="right"
      >
        <div
          class="hamburger"
          :class="{ active: ifShowMobileNav }"
          @click="handleMobileNav"
        >
          <span></span><span></span><span></span>
        </div>
      </el-col>
    </el-row>
    <div class="mobile-header" :class="{ active: ifShowMobileNav }">
      <Nav class="links" />
      <el-button type="primary" plain align="center" @click="route2apps">
        App
      </el-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import Logo from "@/components/HeaderComponent/Logo.vue";
import Nav from "@/components/HeaderComponent/Nav.vue";

export default {
  name: "Header",
  components: {
    Logo,
    Nav,
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["ifShowMobileNav"]),
  },
  methods: {
    ...mapMutations(["setShowMobileNav"]),
    handleMobileNav() {
      this.setShowMobileNav(!this.ifShowMobileNav);
    },
    route2apps() {
      this.setShowMobileNav(false);
      this.$router.push({ path: "/dashboard" });
    },
  },
  watch: {
    $route() {
      this.setShowMobileNav(false);
    },
  },
};
</script>

