const state = {
    ifShowMobileNav: false,
    fullscreenLoading: false,
};

const mutations = {
    setShowMobileNav(state, showorhide) {
        state.ifShowMobileNav = showorhide;
    },
    setShowFullscreenLoading(state, showorhide) {
        state.fullscreenLoading = showorhide;
    }
};

const actions = {};

export default {
    state,
    mutations,
    actions
}
